import axios from "axios";

type sentenceType = {
  part: string,
  sentence: string,
  sentenceTrans: string,
  dummyWord: string,
}

type grammerType = {
  id: string,
  part: string,
  sentence: string,
  sentenceTrans: string,
  dummyWord: string,
}

export default class GrammerData {
  static setGrammerData() {
    const sheetId = '10QM4n6jg93YevAhr-yS8SFp8VjaK_lv09VF_HfFxTYk';
    const sheetName = 'csv_new_horizon_j1';
    const apiKey = 'AIzaSyD61LRLY0MGuJyIU9EQCb4KUDiFJRmxO1o';
    const sentences:grammerType[] = [];
    return axios
    .get('https://sheets.googleapis.com/v4/spreadsheets/'+sheetId+'/values/'+sheetName+'?key='+apiKey)
    .then(response => {
      if (!Array.isArray(response.data.values)) {
        return sentences;
      }
      const responseWords = response.data.values;
      responseWords.shift();
      responseWords.forEach((w: any) => {
        sentences.push({
          id: w[0],
          part: w[1],
          sentence: w[2],
          sentenceTrans: w[3],
          dummyWord: w[4],
        })
      })
      sessionStorage.setItem('SENTENCES', JSON.stringify(sentences));
    })
    .catch(() => sentences);
  }

  static getGrammerData() {
    const storageSentences = sessionStorage.getItem('SENTENCES');
    const sentences:grammerType[] = [];
    if (storageSentences != null && storageSentences.length > 0) {
      JSON.parse(storageSentences).forEach((s:any) => {
        sentences.push({
          id: s['id'],
          part: s['part'],
          sentence: s['sentence'],
          sentenceTrans: s['sentenceTrans'],
          dummyWord: s['dummyWord'],
        })
      });
    }
    return sentences;
  }

  static getFirstPart() {
    return this.getGrammerData().reduce((val:string[], cur) => {
      if (!val.includes(cur.part)) {
        val.push(cur.part);
      }
      return val;
    }, [])[0];  
  }

  static getParts() {
    return this.getGrammerData().reduce((val:string[], cur) => {
      if (!val.includes(cur.part)) {
        val.push(cur.part);
      }
      return val;
    }, []);  
  }

  static getSentences() {
    return this.getGrammerData().reduce((val:sentenceType[], cur) => {
      const w:sentenceType = {
        part: cur.part,
        sentence: cur.sentence, 
        sentenceTrans: cur.sentenceTrans,
        dummyWord: cur.dummyWord,
      };

      if (!val.some(v => 
        v.part === w.part && v.sentence === w.sentence)) {
        val.push(w);
      }
      return val;
    }, []);  
  }
}
