import React, { useState } from 'react';
import { Container } from '@material-ui/core';
import EnglishData from '../context/EnglishData';
import Word from '../components/WordPage/Word';
import Plural from '../components/WordPage/Plural'
import Sentence from '../components/WordPage/Sentence';
import Paginate from '../components/Pagination';
import Part from '../components/Part';
import Favorite from '../components/Common/Favorite';
import SideMenu from '../components/SideMenu';
import { useSwipeable } from "react-swipeable";
import { getPlural, getSingular } from 'plury';

const WordPage: React.FC<{}> = () => {
  const firstPart = sessionStorage.getItem('wordCurrentPart') ?? EnglishData.getFirstPart();
  const [slides, setSlides] = useState(EnglishData.getWordData().filter(w => w.part === firstPart));
  const [wordIndex, setWordIndex] = useState<number>(0);
  const [currentPart, setCurrentPart] = useState<string>(firstPart);
  const [visibleWord, setVisibleWord] = useState<string>('word-hidden');
  const [visiblePlural, setVisiblePlural] = useState<string>('word-hidden');
  const [visibleSentence, setVisibleSentence] = useState<string>('word-hidden');
  const [isInputWordCompleted, setIsInputWordCompleted] = useState<boolean>(false);
  const [isInputPluralCompleted, setIsInputPluralCompleted] = useState<boolean>(false);
  const [inputWordString, setInputWordString] = useState<string>('');
  const [inputPluralString, setInputPluralString] = useState<string>('');
  const [speechSpeedRate, setSpeechSpeedRate] = useState<number>(0.8);

  const slidePrev = () => {
    if (wordIndex > 0) {
      const newIndex = wordIndex-1
      setWordIndex(newIndex);
      sessionStorage.setItem('wordWordIndex', newIndex.toString());
      setVisibleWord('word-hidden');
      setVisiblePlural('word-hidden');
      setVisibleSentence('word-hidden');
      setIsInputWordCompleted(false);
      setInputWordString('');
      setInputPluralString('');
    }
  }

  const slideNext = () => {
    if (wordIndex < slides.length-1) {
      const newIndex = wordIndex+1;
      setWordIndex(newIndex);
      sessionStorage.setItem('wordWordIndex', newIndex.toString());
      setVisibleWord('word-hidden');
      setVisiblePlural('word-hidden');
      setVisibleSentence('word-hidden');
      setIsInputWordCompleted(false);
      setInputWordString('');
      setInputPluralString('');
    }
  }

  const changeInputWordCompleted = (val: boolean) => {
    setIsInputWordCompleted(val);
  }

  const changeInputPluralCompleted = (val:boolean) => {
    setIsInputPluralCompleted(val);
  }

  const changeInputWord = (val: string) => {
    setInputWordString(val);
  }

  const changeInputPlural = (val: string) => {
    setInputPluralString(val);
  }


  const changeCurrentPart = (val: string) => {
    if (currentPart !== val) {
      const s = EnglishData.getWordData().filter(w => w.part === val);
        setSlides(s)
        setCurrentPart(val);
        sessionStorage.setItem('wordCurrentPart', val);
        setWordIndex(0);
        sessionStorage.setItem('wordWordIndex', '0');
        setVisibleWord('word-hidden');
        setVisibleSentence('word-hidden');
        setIsInputWordCompleted(false);
        setInputWordString('');
        setInputPluralString('');
      }
  }

  const changeSpeechSpeedRate = (val: number) => {
    setSpeechSpeedRate(val);
  }

  const swipeHandles = useSwipeable({
    onSwipedLeft: (e) => {
      if (e.absX >= 140 ) {
        slideNext();
      }
    },
    onSwipedRight: (e) => {
      if (e.absX >= 140) {
        slidePrev();
      }
    },
    preventDefaultTouchmoveEvent: true,
    trackMouse: true
  });

  return (
    <>
      <SideMenu
        parts={EnglishData.getParts()}
        currentPart={currentPart}
        changeCurrentPart={(val) => changeCurrentPart(val)}
        speechSpeedRate={speechSpeedRate}
        changeSpeechSpeedRate={(val) => changeSpeechSpeedRate(val)}
      />
      <Container maxWidth="sm">
        <Paginate
          index={wordIndex}
          maxPage={slides.length}
          onClickPrev={slidePrev}
          onClickNext={slideNext}
        />
        <hr/>
        <div {...swipeHandles}>
          <Word
            word={slides[wordIndex].word}
            partOfSpeech={slides[wordIndex].partOfSpeech}
            trans={slides[wordIndex].trans}
            visibleWord = {visibleWord}
            isInputWordCompleted={isInputWordCompleted}
            inputWordString={inputWordString}
            onClick={() => setVisibleWord('word-visible')}
            changeInputWordCompleted={(val) => changeInputWordCompleted(val)}
            changeInputWord={(val) => changeInputWord(val)}
          />
          {
            slides[wordIndex].plural
            &&
            <Plural
              plural={slides[wordIndex].plural}
              partOfSpeech={slides[wordIndex].partOfSpeech}
              trans={slides[wordIndex].trans}
              visiblePlural = {visiblePlural}
              isInputPluralCompleted={isInputPluralCompleted}
              inputPluralString={inputPluralString}
              onClick={() => setVisiblePlural('word-visible')}
              changeInputPluralCompleted={(val) => changeInputPluralCompleted(val)}
              changeInputPlural={(val) => changeInputPlural(val)}
            />
          }
        </div>
        <hr/>
        <Sentence
          sentence={slides[wordIndex].sentence}
          trans={slides[wordIndex].sentenceTrans}
          visibleSentence={visibleSentence}
          speechSpeedRate={speechSpeedRate}
          onClick = {() => setVisibleSentence('word-visible')}
        />
        <hr/>
        <Favorite 
          category='Word'
          part={currentPart}
          id={slides[wordIndex].id}
        />
        <hr/>
        <Part
          currentPart={currentPart}
        />
      </Container>
    </>
  )
} 

export default WordPage;