import React from 'react';
import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
import * as colors from "@material-ui/core/colors";
import CssBaseline from "@material-ui/core/CssBaseline";
import 'fontsource-roboto';
import GlobalMenu from './components/GlobalMenu';
import WordPage from './pages/WordPage';
import WordTransPage from './pages/WordTransPage';
import WordGrammerPage from './pages/WordGrammerPage';
import GrammerPage from './pages/GrammerPage';
import EnglishData from './context/EnglishData';
import GrammerData from './context/GrammerData';

interface AppProps {
}

interface AppState {
  currentPage: pageNames
}

class App extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    const sPage:readonly string[] = ['WordPage'];
    const cPage:pageNames = 'WordPage';
    this.state = {
      currentPage: cPage
    }
    EnglishData.setWordData();
  }

  render () {
    const theme = createMuiTheme({
      palette: {
        primary: {
          main: colors.blue[800],
        },
        type: 'dark',
      },
    });

    const changePage = (val: pageNames) => {
      this.setState({
        currentPage: val
      })
      if (val === 'WordPage') {
        EnglishData.setWordData();
      } else if (val === 'WordTransPage') {
        GrammerData.setGrammerData();
      }
    }

    return (
      <ThemeProvider theme={theme}>
        <CssBaseline />
        {
          this.state.currentPage === 'WordPage'
          &&
          <WordPage />
        }
        {
          this.state.currentPage === 'WordTransPage'
          &&
          <WordTransPage />
        }
        {
          this.state.currentPage === 'WordGrammerPage'
          &&
          <WordGrammerPage />
        }
        {
          this.state.currentPage === 'GrammerPage'
          &&
          <GrammerPage />
        }
        <GlobalMenu
            currentPage={this.state.currentPage}
            changeCurrentPage= {(val) => changePage(val)}
        />
      </ThemeProvider>
    );
  }
}

export default App;